import Watcher from "../../../Watcher/Watcher";
function compilerTextNode(node, context) {
    let text = node.textContent.trim();
    if (text) {
        // text
        let exp = parseTextExp(text);
        // 
        new Watcher(exp, context, (newValue) => {
            // 
            // console.log(newValue);
            node.textContent = newValue;
        });
    }
}
function parseTextExp(text) {
    // 
    let regText = /\{\{(.+?)\}\}/g;
    // 
    var pices = text.split(regText);
    // data
    var matches = text.match(regText);
    // 
    let tokens = [];
    // item``()push
    pices.forEach((item) => {
        if (matches && matches.indexOf("{{" + item + "}}") > -1) {
            // data
            tokens.push('(' + item + ')');
        }
        else {
            tokens.push("`" + item + "`");
        }
    });
    return tokens.join("+");
}
export default compilerTextNode;
