import Watcher from "../../../../../Watcher/Watcher";
export default function showHelp(attrValue, context, node) {
    // display
    let originalDisplay = window.getComputedStyle(node)["display"];
    // showfalsedispalynone   showtrue
    new Watcher(attrValue, context, (newValue) => {
        node.style.display = newValue ? originalDisplay : "none";
    });
}
