import MVVM from "./Core/MVVM";
import "./style/index.less";
const vm = new MVVM({
    $el: '#root',
    data: {
        activeIndex: 0,
        tabList: ['', '', '', '/'],
        // 
        person: {
            name: '',
            age: 12,
            sex: ''
        },
        // m-if
        showText: false,
        // m-for
        forTable: ["1x1=1", "2x2=4", "3x3=9", "4x4=16", "5x5=25", "6x6=36", "7x7=49", "8x8=64", "9x9=81"],
    },
    computed: {
        // 
        computedDescription() {
            return `${this.person.name}${this.person.age}${this.person.sex}`;
        },
    },
    methods: {
        // showTextm-if
        showTextChange: function () {
            if (this.showText) {
                this.showText = false;
            }
            else {
                this.showText = true;
            }
        },
        // 
        active: function (index) {
            // (this as any).activeIndex = index,
            //   e.target.parentElement.parentElement.childNodes.forEach(child => {
            //     child.classList.remove("active")
            //   })
            // e.target.parentElement.classList.add("active");
            this.activeIndex = index;
        },
        // 
        initForTable: function (s) {
            alert(s);
        },
        init: function () {
            console.log("1111111");
        }
    },
});
window['vm'] = vm;
