import Watcher from "../../../../Watcher/Watcher";
function methodsHandle(attrName, attrValue, context, node) {
    // 
    let para = [];
    // 
    if (attrValue.indexOf("(") !== -1) {
        let argu;
        [attrValue, argu] = attrValue.split("(");
        [argu,] = argu.split(")");
        // 
        argu = argu.split(/,(?![^{}]*})/g);
        //  
        para = evalArguments(argu, context);
    }
    // 
    compilerMethods(context, node, attrName, attrValue, para);
    node.removeAttribute(attrName);
}
function compilerMethods(scope, node, attrName, attrValue, para = []) {
    // typeclick
    // fnhandleClick
    // fn.bind(scope) vm
    // 
    let type = attrName.slice(1);
    // methods
    let fn = scope[attrValue];
    // 
    node.addEventListener(type, function (e) {
        para.forEach((p, index) => {
            if (p === "$event") {
                para[index] = e;
            }
        });
        fn.bind(scope)(...para);
    });
}
function evalArguments(argus, context) {
    let para = [];
    argus.forEach(a => {
        a = a.trim();
        // 
        if (a === "$event") {
            para.push("$event");
        }
        // data
        else if (Watcher.computeExpression(a, context)) {
            para.push(Watcher.computeExpression(a, context));
        }
        else {
            // 
            if (a[0] == '{' && a[a.length - 1] == '}') {
                a = eval("(" + a + ")");
                para.push(eval(a));
            }
            else
                para.push(eval(a));
        }
    });
    return para;
}
export default methodsHandle;
