import Dep from '../Dep/Dep';
var $uid = 0;
export default class Watcher {
    constructor(exp, scope, cb) {
        this.uid = $uid;
        this.scope = scope;
        this.cb = cb;
        this.exp = exp;
        // 
        this.update();
    }
    get() {
        // Dep.targetDep.targetWatcher
        Dep.targetObject = this;
        // 
        let newValue = Watcher.computeExpression(this.exp, this.scope);
        // 
        Dep.targetObject = null;
        return newValue;
    }
    update() {
        // 
        let newValue = this.get();
        // 
        this.cb && this.cb(newValue);
    }
    static computeExpression(exp, scope) {
        // 
        // scope
        // with
        // 
        // dataObservegetdep.targetthiswatcherdep
        // scope scope
        let fn = new Function("scope", "with(scope){return " + exp + "}");
        return fn(scope);
    }
}
