import Dep from "../Dep/Dep";
var uid = 0;
export default class ObserverComputed {
    constructor(computed, scope) {
        this.computed = computed;
        this.scope = scope;
        // 
        this.walk(this.computed);
    }
    walk(computed) {
        if (!computed || typeof computed !== "object") {
            return;
        }
        Object.keys(computed).forEach((key) => {
            // 
            this.defineReactive(computed, key, computed[key], this.scope);
        });
    }
    defineReactive(computed, key, exp, scope) {
        // 
        Object.defineProperty(exp, "val", {
            value: "",
            writable: true
        });
        //  false true
        Object.defineProperty(exp, "dirty", {
            value: true,
            writable: true
        });
        // 
        Object.defineProperty(exp, "uid", {
            value: uid++,
            writable: true,
        });
        Object.defineProperty(computed, key, {
            // 
            enumerable: true,
            // writable: false,
            // 
            configurable: false,
            get: () => {
                // true
                if (exp.dirty) {
                    // false
                    exp.dirty = false;
                    // 
                    Dep.targetFunction = exp;
                    // 
                    exp.val = exp.bind(scope)();
                    // Dep
                    Dep.targetFunction = null;
                    // 
                    return exp.val;
                }
                else {
                    return exp.val;
                }
            },
            // 
            set: (newval) => {
                console.log("No writable");
            }
        });
    }
}
