import Watcher from "../../../../../Watcher/Watcher";
export default function modelHelp(attrValue, context, node) {
    //   input input inputdata
    new Watcher(attrValue, context, (newValue) => {
        node.value = newValue;
    });
    // input 
    node.addEventListener("input", (e) => {
        let newValue = e.target.value;
        let express = attrValue.split('.');
        // 
        return express.reduce((pre, next, currentIndex) => {
            if (currentIndex === express.length - 1) {
                // data
                return pre[next] = newValue;
            }
            return pre[next];
        }, context.$data);
    });
}
