import Dep from "../Dep/Dep";
import ArrayProto from "./arrayDefine";
export default class ObserverData {
    constructor(data) {
        this.data = data;
        // 
        this.walk(this.data);
    }
    walk(data) {
        // return
        if (!data || typeof data !== "object") {
            return;
        }
        Object.keys(data).forEach((key) => {
            //  __proto__ m-for
            if (typeof data[key] === 'object' && data[key][0] !== undefined) {
                data[key].__proto__ = new ArrayProto(null).arrayProto;
            }
            // 
            this.defineReactive(data, key, data[key]);
        });
    }
    defineReactive(data, key, value) {
        var dep = new Dep();
        Object.defineProperty(data, key, {
            // 
            enumerable: true,
            //   m-for
            configurable: true,
            // 
            get: () => {
                //  Watcher, getwatchergetcomputeExpression, watcher
                // Dep.targetObject
                // Dep.targetFunction
                Dep.targetFunction && dep.addSub(Dep.targetFunction);
                Dep.targetObject && dep.addSub(Dep.targetObject);
                return value;
            },
            // 
            set: (newValue) => {
                value = newValue;
                // Watcherupdate
                dep.notify();
            },
        });
        // value
        this.walk(value);
    }
}
