export default class Dep {
    constructor() {
        this.subs = [];
    }
    addSub(target) {
        // 
        while (this.subs[target.uid] !== undefined && target !== this.subs[target.uid]) {
            target.uid++;
        }
        // 
        this.subs[target.uid] = target;
    }
    notify() {
        for (let uid in this.subs) {
            // 
            if (typeof this.subs[uid] == "object") {
                this.subs[uid].update();
            }
            else {
                // true
                this.subs[uid].dirty = true;
            }
        }
    }
}
